import axios from "axios"
export default {
    namespaced:true,
    state: {
        data:{
            students: [],
            total: 0,
            curPage: 1,
            isLoading: false
        }
        
    },
    mutations:{
        setData(state, obj){
            state.data = {
                ...state.data,
                ...obj
            }
        },
        deleteData(state, id){
            state.data.students = state.data.students.filter(it=>it._id!==id);
            state.data.total--;
        }
    },
    actions:{
        async fetch(context, newPage = 1){
            context.commit("setData", {
                curPage:newPage,
                isLoading:true
            })
            let resp = await axios.get("/api/student", {params:{page:newPage}});
            resp = resp.data;

            context.commit("setData", {
                total: resp.count,
                students: resp.data,
                isLoading:false
            })
        },
        async remove(context, id){
            await axios.delete("/api/student/"+id);
            context.commit("deleteData", id);
        }
    }
}