import axios from "axios"
export default {
    namespaced:true, //打开命名空间
    state:{
        data:[]
    },
    mutations:{ 
        setData(state, data){
            state.data = data;
        }
    },
    actions:{
        async fetch(context){
            const resp = await axios.get("/api/course");
            context.commit("setData", resp.data.data)
        }
    }
}