import axios from "axios"
import Cookies from "js-cookie"

export default {
    namespaced:true,
    state: {
        data: null
    },
    mutations: {
        setData(state, user) {
            state.data = user;
        }
    },
    actions: {
        async login(context, loginId, loginPwd){
            const resp = await axios.post("/api/user/login", {
                loginId,
                loginPwd
            })
            if(resp.data){
                //登录成功
                context.commit("setData", resp.data);
                return true;
            }
            return false;
        },
        async loginOut(context){
            //1. 设置data为null
            context.commit("setData", null);
            //2. 删除对应的cookie
            Cookies.remove("uid");
        },
        async whoAmI(context){
            const resp = await axios.get("/api/user/whoami")
            if(resp.data){
                context.commit("setData", resp.data);
            }
        }
    }
}